import React from "react";
import { Container, Row, Col } from 'reactstrap';

import Image from "../components/image";
import Layout from "../components/layout";
import SEO from "../components/seo";
import "../../image.css"
export default ({data}) => {
    let personData = data.allSitePage.edges[0].node.context;
    console.log(personData.photo);
    return (
        <Layout>
            <Container>
                <Row className={'center'}>
                    <Col>
                        <h1>{personData.name}</h1>
                        <h3>
                            {personData.position}
                        </h3>
                    </Col>
                    <Col>
                        <Image photoToUse={personData.photo}/>
                        <p>
                            {personData.bio}
                        </p>
                    </Col>
                </Row>



            </Container>

        </Layout>
    )
}

export const query = graphql`
    query($path: String!) {
        allSitePage(filter: { path: {eq: $path } }) {
            edges {
                node {
                    context {
                        name
                        position
                        bio
                        photo
                    }
                }
            }
        }
    }
`;